body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.chatbot {
  width: 100%;
  max-width: 600px;
  margin: auto;
  padding: 20px;
  line-height: 1.5;
}
.chatbot h1 {
  font-size: 30px;
}

.chatbot .response {
  background: rgb(230, 230, 230);
  color: #000;
  padding: 10px;
  border-radius: 10px;
  width: 100%;
  margin: 20px 0;
  max-width: fit-content;
  white-space: break-spaces;
}

.chatbot .prompt {
  background: #04AA6D;
  color: #fff;
  padding: 10px;
  border-radius: 10px;
  margin-left: auto;
  width: 100%;
  max-width: fit-content;
}
input[type="text"] {
  width: 100%;
  padding: 10px;
  margin-top: 20px;
  border-radius: 10px;
  height: 30px;
  border: 1px solid #ddd;
  box-shadow: 0 0 transparent,0 0 transparent, 0 0 15px rgba(0,0,0,.1);
}

.lds-ring {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  margin: auto;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 32px;
  height: 32px;
  margin: 2px;
  border: 2px solid #000;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #000 transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

nav {
  background: #efefef;
}
.menu {
  display: flex;
  font-size: 20px;
  gap: 20px;
  list-style: none;
  justify-content: space-between;
  padding: 20px;
  background: #efefef;
  margin: 0;
  max-width: 1440px;
  margin: auto;
}

.menu a {
  text-decoration: none;
}

.titleScore {
  display: flex;
  justify-content: space-between;
}

.container {
  max-width: 600px;
  margin: 0 auto;
}

.ai-video {
  width: 320px;
}

.graph {
  width: 720px;
  margin: auto;
}

.get-started {
  background: rgba(20, 180, 87,0.8);
  border-radius: 3px;
  padding: 22px;
  border: 0 none;
  cursor: pointer;
  font-size: 18px;
}

.center {
  text-align: center;
}

.space-top {
  margin-top: 20px;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.fade-in {
    animation: fadeIn 0.5s;
}
@keyframes slideIn {
    from { transform: translateX(100%); }
    to   { transform: translateX(0); }
}

.slide-in {
    animation: slideIn 0.5s;
}

